<template>
  <!-- заменяем  $store.getters.cartDetailProducts на выч свойство -->
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" alt="item.title" />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      {{ item.product.productId }}
    </span>

    <div class="item__row">
      <counter-product :count="amount" @change="handleChange"/>

    </div>

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }}
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(item.productId)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
// import { mapMutations } from 'vuex'     //проксируем обаботчики мутации в наш компонент
import CounterProduct from '@/components/CounterProduct.vue';
import { mapActions } from 'vuex'
export default {

  components: { CounterProduct },
  filters: { numberFormat },
  props: ["item"],
  computed: {
    amount: {
      get() {
        return this.item.amount
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value } )    //вызываем мутацию
      }
    }
  },
  methods: {
    ...mapActions(['deleteCartProduct']) ,  //проксируем метод deleteProduct в actions deleteCartProduct - уменьшает код
    handleChange(value) {
      if(value > 0) {
        this.amount = value
      }

    },
    deleteProduct() {
      this.deleteCartProduct({ productId: this.item.productId})
      // .then(() => this.$store.commit('deleteCartProduct',))

    }
  }
};
</script>
