<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>

    <div class="content__catalog">
      <!--было до шины событий передаём значения которое хранится  в св-ве сосотояние можно исп модификатор sync
      @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams )" -->
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :color-checked.sync="filterColor"
      />
      <section class="catalog">
        <div v-if="productsLoading" class='sk-rotating-plane'></div>

        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров...
          <button @click.prevent="loadProducts">Попробовать ещё раз</button>
        </div>

        <ProductList :products="products" />
        <BasePagination
          v-model="page"
          :count="countProducts"
          :per-page="productPerPage"
        />
        {{ colorProductChecked }}
      </section>
    </div>
  </main>
</template>

<script>
/* eslint-disable */
// import products from "@/data/products"; // @ - абс путь на папку src , относительные пути раньше были относительно app.vue и ь
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/PoductFilter.vue";
import axios from "axios";
import { API_BASE_URL } from '../config'

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterCategoryId: 0,
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterColor: 0,
      colorProductChecked: " ",
      colorProductCheckedId: null,
      page: 1,
      productPerPage: 3,
      productsData: null,
      productsLoading: true,
      productsLoadingFailed: false,
    };
  },
  computed: {

    products() {
      // вычисляем старт и кон индекс
      // const offset = (this.page - 1) * this.productPerPage;
      // return this.filteredProducts.slice(offset, offset + this.productPerPage);
      return this.productsData
        ? this.productsData.items.map((product) => {
            return {
              ...product,
              image: product.image.file.url,
            };
          })
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
      // this.filteredProducts.length;
    },
  },
  methods: {
    loadProducts() {
      console.log('this.filterColor=', this.filterColor)
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer)  //отменяем вызов предыдущего таймера и вызовется только последний метод loadProducts
     this.loadProductsTimer = setTimeout(() => {  //св-во экземпляра объекта чтобы в фильтре 3 раза не вызывалось получение данных с сервера
            axios
        .get(API_BASE_URL + `/api/products`, {
          params: {
            page: this.page,
            limit: this.productPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColor,
          }, // параметры запроса, чтобы не писать в цепочку
        })
        .then((response) => (this.productsData = response.data))
        .catch(() => this.productsLoadingFailed = true)
        .then(() => this.productsLoading = false)
      },5000)
    },
  },
  watch: {
    page() {
      //при изменении пагинац2ии загружаем новую страницу
      this.loadProducts();
    },
    filterPriceFrom() { // при изм цены от вызываем запрос на сервер
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColor(){
      this.loadProducts();
    }
  },
  created() {
    this.loadProducts();
  },
};

</script>
