import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import { API_BASE_URL } from "../config";
// import router from "../router";

Vue.use(Vuex);


export default new Vuex.Store({
  state: {
    cartProducts: [
      // {
      //   productId: 0, amount: 0
      // }
    ],
    userAccessKey: null,  //ключ польз
    cartProductsData: [],
    colorsData: [],
    orderInfo: null
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo
    },
    resetCart(state) {
      state.cartProducts = []
      state.cartProductsData = []
    },
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((item) => item.productId === productId);

      if (item) {
        item.amount = amount;
      }
    },

    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter(item => item.productId !== productId)
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          amount: item.quantity
        }
      })
    },
    updateColorsData(state, items) {
      state.colorsData = items;
    }

  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map(item => {
        const product = state.cartProductsData.find(p => p.product.id === item.productId).product

        return {
          ...item,
          product
            : {
            ...product,
            image: product.image.file.url     // подмешать image
          },
        }
      });
    },
    cartTotalPrice(state, getters) {
       return getters.cartDetailProducts.reduce((acc, item) => item.product.price * item.amount + acc, 0);
    },
    getColorsData(state) {
        return state.colorsData
    },
   },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios.get(API_BASE_URL + '/api/orders/'+ orderId, {
        params: {
          userAccessKey: context.state.userAccessKey
        }
      })
      .then(response => {
        context.commit('updateOrderInfo', response.data)
      })

    },
    loadCart(context) {
      return axios.get(API_BASE_URL + '/api/baskets', {  //взвращаем промиз
        params: {
          userAccessKey: context.state.userAccessKey
        }
      })
        .then(response => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey)
            context.commit('updateUserAccessKey', response.data.user.accessKey)
          }
          context.commit('updateCartProductData', response.data.items)
          context.commit('syncCartProducts')

        })
    },
    addProductToCart(context, { productId, amount }) {
      return (new Promise(resolve => setTimeout(resolve, 1000)))  // делаем задержку вызова
        .then(() => {
          return axios.post(API_BASE_URL + '/api/baskets/products', {
            productId: productId,
            quantity: amount
          }, {
            params: {
              userAccessKey: context.state.userAccessKey
            }
          })
            .then(response => {
              context.commit('updateCartProductData', response.data.items)
              context.commit('syncCartProducts')
            })
        })
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount })  // сразу обновляем корзину

      return axios.put(API_BASE_URL + '/api/baskets/products', {
        productId: productId,
        quantity: amount
      }, {
        params: {
          userAccessKey: context.state.userAccessKey
        }
      })
        .then(response => {
          context.commit('updateCartProductData', response.data.items)
        })
        .catch(() => context.commit('syncCartProducts'))  //если ввести в кол не число а строку , то произойдет откат на значение поля
    },

    deleteCartProduct(context, {productId}){
      return axios.delete(API_BASE_URL + '/api/baskets/products',  {
        data: {
          productId: productId,
        },
        params: {
          userAccessKey: context.state.userAccessKey,
         }
      })
      .then(response => {
        console.log('response.data.items=',response.data.items)
        context.commit('updateCartProductData', response.data.items)
        context.commit('deleteCartProduct', productId)
      })
      .then(() => context.commit('syncCartProducts') )
      .catch(() => alert('huli'))
      .then(() => {
        // window.location.reload()
        // this.$router.go(this.$router.currentRoute)
      }
      )
    },
    loadColors(context,  ) {
      return axios.get(API_BASE_URL + '/api/colors',  )
      .then((response) => {
        console.log('response.data=', response.data.items)
       context.commit('updateColorsData', response.data.items )})
    }
  }
});
