<template>
  <div class="cart__block">
    <ul class="cart__orders" >
      <li class="cart__order" v-for="item in cartProduct" :key="item.productId">
        <h3>{{ item.product.title }} </h3>
        <b>{{ item.product.price *  item.amount  }} </b>
        <span>Артикул: {{ item.productId }} </span>
      </li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p v-if="totalPrice" >Итого: <b> {{ cartProduct.length }} </b> товара на сумму <b>{{ totalPrice }} ₽</b></p>
    </div>
   {{disableButtonZakaz}}
    <button class="cart__button button button--primery" v-if="!disableButtonZakaz" type="submit">
      Оформить заказ
    </button>
  </div>
</template>

<script>
export default {
  props: ['cartProduct', 'disableButtonZakaz'],
   computed: {
       totalPrice() {
         if(this.$store.state.orderInfo){
          return this.$store.state.orderInfo.totalPrice}
       }
    }
};
</script>
