/* eslint-disable */
<template>
 <!-- eslint-disable -->
  <ul class="catalog__pagination pagination">
          <li class="pagination__item">
           <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled' : page === 1}"
            aria-label="Предыдущая страница" @click.prevent="paginateLeft(page)" href="#">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <!-- директива v-bind для класса -->
            <a href="#" class="pagination__link" :class="{'pagination__link--current' : pageNumber === page}"
            @click.prevent="paginate(pageNumber)">
              {{ pageNumber }}
            </a>
          </li>

          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" href="#" @click.prevent="paginateRight(page, pages)" :class="{'pagination__link--disabled' : page === pages }"
            aria-label="Следующая страница">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
        </ul>
</template>

<script>
// eslint-disable
export default {
  model: { // настройка директивы v-model для page
    prop: 'page', // свойство , которое передаётся будет записываться v page
    event: 'paginate', // событие которое будем передавать наз paginate
  },
  props: ['page', 'count', 'per-page'], // входящие параметры, которые будет принимать компонент
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage); // округляем в большую сторону
    },
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page); // связываем двунаправленное свойство page и отправляем его в v-model в app.vue с событием paginate
      // передаем номер страницы на которую хотим переключиться
    },
    paginateRight(page, pages) {
    /*eslint-disable */
      if(page < pages) {
        this.$emit('paginate', page+1);
      }
    },
    paginateLeft(page) {
      if(page > 1){
        this.$emit('paginate', page-1)
      }
    }

  },
};
</script>
