<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html"> Корзина </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ orderNumber }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет
            письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами
            в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary" v-if="orderInfo">
            <li class="dictionary__item">
              <span class="dictionary__key"> Получатель </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Адрес доставки </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Телефон </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Способ оплаты </span>
              <span class="dictionary__value"> картой при получении </span>
            </li>
          </ul>
        </div>
        <OrderItem
          :cartProduct="cartProduct"
          :disableButtonZakaz="disableButtonZakaz"
        />
      </form>
    </section>
  </main>
</template>
<script>
import OrderItem from "@/components/OrderItem.vue";
export default {
  data() {
    return {
      disableButtonZakaz: true,
    };
  },
  components: { OrderItem },
  computed: {
    orderNumber() {
      return this.$route.params.id;
    },

    orderInfo() {
      if (this.$store.state.orderInfo) {
        return this.$store.state.orderInfo;
      }
    },
    cartProduct() {
    // console.log(
    //       "this.$store.state.orderInfo=",
    //       this.$store.state.orderInfo
    //     );
      if (this.$store.state.orderInfo) {

        const cartProduct1 = this.$store.state.orderInfo.basket.items;
        const cartProduct2 = cartProduct1.map((item) => {
          //подмешаем amount , которого нет на сервере
          {
            return {
              ...item,
              amount: item.quantity,
              productId: item.product.id,
           };
          }
        });
      return cartProduct2
     }
    }
  },
  created() {
    this.$store.dispatch("loadOrderInfo", this.$route.params.id); //при перезапуске страницы получаем инфу о заказе по id из пути
   },
};
</script>
